/* ----------------------------
   GLOBAL CONTAINER
-----------------------------*/
.birthday-card-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  font-family: 'Segoe UI', sans-serif;
  overflow: hidden;
  position: relative;
}

/* ----------------------------
   CARD BASE
-----------------------------*/
.birthday-card {
  width: 100%;
  max-width: 900px;
  height: auto;
  aspect-ratio: 16/10;
  max-height: 90vh;
  background: white;
  border-radius: 25px;
  transform-style: preserve-3d;
  transition: 0.8s ease;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  box-shadow: 0 20px 40px rgba(0,0,0,0.2);
}

.birthday-card.open {
  transform: rotateY(180deg);
}

/* Shared face styles */
.card-front,
.card-inside {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  flex-direction: row;
}

/* ----------------------------
   FRONT CARD
-----------------------------*/
.card-front {
  background: #fff;
  border-radius: 25px;
  overflow: hidden;
}

.front-image-section {
  flex: 1;
  position: relative;
}

.front-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.front-text-section {
  flex: 1;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.front-title {
  font-size: 32px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 15px;
}

.front-subtitle {
  color: #666;
  margin-bottom: 25px;
}

.cta-button {
  padding: 14px 24px;
  border-radius: 30px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  font-weight: 600;
  display: inline-flex;
  gap: 8px;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

/* ----------------------------
   INSIDE CARD
-----------------------------*/
.card-inside {
  transform: rotateY(180deg);
  background: #fff;
  border-radius: 25px;
}

/* left panel */
.inside-left {
  flex: 1;
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.photo-wrapper {
  width: 100%;
  max-width: 380px;
  border-radius: 18px;
  overflow: hidden;
  box-shadow: 0 15px 30px rgba(0,0,0,0.2);
}

/* right panel */
.inside-right {
  flex: 1;
  padding: 35px;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.birthday-title {
  font-size: 30px;
  text-align: center;
  background: linear-gradient(135deg, #667eea, #764ba2);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.main-message,
.highlight-message {
  font-size: 15px;
  line-height: 1.6;
  color: #444;
}

.highlight-box {
  background: #ffe0ef;
  padding: 18px;
  border-left: 5px solid #d94a9c;
  border-radius: 12px;
}

/* ----------------------------
   ✅ RESPONSIVE FIXES
-----------------------------*/

/* ✅ MOBILE – stack layout */
@media (max-width: 768px) {
  .card-front,
  .card-inside {
    flex-direction: column;
  }

  .birthday-card {
    aspect-ratio: unset;
    height: auto;
  }

  .front-image-section {
    height: 220px;
  }

  .inside-left, .inside-right {
    width: 100%;
    padding: 20px;
  }

  .photo-wrapper {
    max-width: 90%;
  }

  .birthday-title {
    font-size: 26px;
  }
}

/* ✅ SMALL MOBILE */
@media (max-width: 480px) {

  .front-title {
    font-size: 24px;
  }

  .cta-button {
    padding: 10px 18px;
    font-size: 13px;
  }

  .front-image-section {
    height: 180px;
  }

  .birthday-title {
    font-size: 22px;
  }

  .main-message {
    font-size: 14px;
  }
}
